@font-face {
  font-family: 'Orbitron';
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/orbitron-v25-latin-regular.woff2") format("woff2"), url("../fonts/orbitron-v25-latin-regular.woff") format("woff");
}

* {
  box-sizing: border-box;
  margin: 0;
}

.body {
  font-family: 'Orbitron', 'Arial', sans-serif;
  font-style: normal;
  font-weight: normal;
}

.container {
  width: 1440px;
  padding: 0 60px;
  margin: 0 auto;
}

.img-responsive {
  width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.background {
  background: linear-gradient(to right, #000000 52%, #57B8FF 48%);
}

.header {
  background-color: #000000;
  position: fixed;
  display: flex;
  align-items: center;
  z-index: 10;
  align-items: center;
  z-index: 10;
  width: -webkit-fill-available;
  justify-content: center;
}

.header__meny {
  list-style: none;
  display: flex;
  padding-right: 100px;
}

.header__logo {
  padding-right: 165px;
  cursor: pointer;
}

.header__meny a {
  font-family: 'Orbitron';
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 26px;
  color: #FFFFFF;
  text-decoration: none;
  padding: 0 30px;
}

.header__meny .header__link-active {
  color: #FAC422;
}

.header__meny a:hover {
  color: #FAC422;
}

.header__btn {
  border: none;
  background-color: transparent;
  cursor: pointer;
  padding-right: 40px;
}

.header__btn svg:hover path {
  fill: #FAC422;
}

.header__contact {
  font-family: 'Orbitron', 'Arial', sans-serif;
  background-color: transparent;
  border: 1px solid #FFFFFF;
  cursor: pointer;
  font-size: 18px;
  line-height: 30px;
  color: #FFFFFF;
  padding: 16px 31px;
}

.header__contact:hover {
  color: #FAC422;
  border: 1px solid #FAC422;
}

.banner {
  padding-top: 180px;
  padding-bottom: 100px;
}

.banner__link {
  list-style: none;
  margin-left: -90px;
}

.banner__link a {
  font-weight: 400;
  font-size: 11px;
  line-height: 25px;
  color: #80858F;
  text-decoration: none;
  writing-mode: vertical-lr;
  transform: rotate(-180deg);
  padding: 20px 0;
  text-transform: uppercase;
}

.banner__link a:hover {
  color: #FAC422;
  text-transform: uppercase;
}

.fire__link {
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  text-transform: uppercase;
  color: #57B8FF;
  position: relative;
  padding-left: 40px;
}

.fire__link::before {
  content: '';
  background-image: url(../img/banner/Fire.png);
  width: 32px;
  height: 32px;
  position: absolute;
  left: 5px;
  top: -10px;
}

.banner__box {
  display: flex;
  align-items: center;
}

.banner__left {
  padding-right: 35px;
}

.banner__text {
  font-weight: 400;
  font-size: 72px;
  line-height: 86px;
  text-transform: capitalize;
  color: #FFFFFF;
  position: relative;
  padding: 40px 0;
}

.banner__text::after {
  content: '';
  background-image: url(../img/banner/line.png);
  position: absolute;
  width: 434px;
  height: 16px;
  top: 210px;
  left: 210px;
}

.button {
  font-family: sans-serif;
  text-decoration: none;
  display: inline-block;
  border: transparent;
  font-weight: 400;
  font-size: 18px;
  line-height: 28px;
  text-transform: capitalize;
  color: #FFFFFF;
  background-color: #57B8FF;
  padding: 20px 30px;
  position: relative;
}

.button::before {
  content: '';
  position: absolute;
  width: 1.5em;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgba(255, 255, 255, 0);
  transform: translateX(0em);
}

.button:hover {
  cursor: pointer;
}

.button:hover:before {
  animation: blick 0.6s;
}

@keyframes blick {
  0% {
    transform: translateX(0em);
  }
  1% {
    background-color: rgba(255, 255, 255, 0.6);
  }
  100% {
    transform: translateX(9em);
  }
}

.sponsors {
