}

.news__card img {
  border-radius: 5%;
}

.news__card {
  width: 312px;
  height: 189px;
  position: relative;
  perspective: 1000px;
  cursor: pointer;
}

.news__front,
.news__back {
  width: 100%;
  height: 100%;
  overflow: hidden;
  backface-visibility: hidden;
  position: absolute;
  transition: transform .6s linear;
}

.news__front {
  transform: rotateY(0deg);
}

.news__back {
  transform: rotateY(180deg);
}

.news__card:hover > .news__front {
  transform: rotateY(-180deg);
}

.news__card:hover > .news__back {
  transform: rotateY(0deg);
}

